.ware {
    float: left; 
    margin: 30px 10px 20px 20px;
    width: 320px;
    text-align: center; 
    border: 1px solid rgba(0, 0, 0, 0.397);
    box-shadow: 6px 5px 5px grey;
    padding: 10px 0 10px 0;
    
}
.photo {
    height: 152px;
}
.img {
    width: 20px;
    height: 20px;
    float: right;
    margin-top: -10px;
    transition: filter 0.2s linear;
    filter: brightness(2) grayscale(0.3) opacity(0.7);
}
.img:hover {
    filter: none;
}
